<template>
  <UserReviewsModal
    v-if="isReviewsModalVisible"
    :user="userForReviews"
    @close="closeReviewsModal"
  />
  <div class="followers-page">
    <div v-if="loading">Loading followers...</div>
    <div v-else>
      <div v-if="followers.length === 0" class="no-followers-message">
        <span>No one is following you yet.</span>
      </div>
      <div v-for="user in followers" :key="user.uid" class="user-card">
        <div class="user-card-content">
          <div class="user-info">
            <img :src="user.photoURL || 'https://cdn-icons-png.flaticon.com/512/149/149071.png'" class="user-avatar" alt="User" />
            <div>
              <span class="user-name">{{ user.displayName }}</span>
            </div>
          </div>
          <div v-if="user.lastReview" class="last-review">
            <div class="review-header">
              Latest review: <strong>{{ user.lastReview.restaurantName }}</strong>
            </div>
            <div class="review-body">
              <StarRating :rating="user.lastReview.rating" />
              <p v-if="user.lastReview.text" class="review-text">"{{ user.lastReview.text }}"</p>
            </div>
          </div>
          <div v-else class="last-review no-review">
            No reviews yet.
          </div>
        </div>
        <div class="user-actions">
          <button @click="openReviewsModal(user)" class="action-btn" title="View Reviews">
            <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 0 24 24" width="24px" fill="currentColor"><path d="M0 0h24v24H0V0z" fill="none"/><path d="M20 2H4c-1.1 0-2 .9-2 2v18l4-4h14c1.1 0 2-.9 2-2V4c0-1.1-.9-2-2-2zm0 14H6l-2 2V4h16v12z"/></svg>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue';
import { db, auth } from '../firebase';
import { collection, query, getDocs, where, orderBy, limit } from 'firebase/firestore';
import UserReviewsModal from './UserReviewsModal.vue';
import StarRating from './StarRating.vue';

export default {
  name: 'Followers',
  components: {
    UserReviewsModal,
    StarRating
  },
  setup() {
    const followers = ref([]);
    const loading = ref(true);
    const isReviewsModalVisible = ref(false);
    const userForReviews = ref(null);

    const fetchFollowers = async () => {
      loading.value = true;
      const currentUser = auth.currentUser;
      if (!currentUser) {
        loading.value = false;
        return;
      }

      // Fetch documents where the current user is being followed
      const followsQuery = query(collection(db, 'follows'), where('followingId', '==', currentUser.uid));
      const followsSnap = await getDocs(followsQuery);
      const followerIds = followsSnap.docs.map(d => d.data().followerId);

      if (followerIds.length === 0) {
        followers.value = [];
        loading.value = false;
        return;
      }

      // Fetch all restaurants to map IDs to names
      const restaurantsSnap = await getDocs(collection(db, 'restaurants'));
      const restaurantsMap = new Map();
      restaurantsSnap.forEach(doc => {
          restaurantsMap.set(doc.id, doc.data().name);
      });

      // Fetch user profiles of the followers
      const usersData = [];
      // Firestore 'in' query is limited to 30 elements, so we batch
      for (let i = 0; i < followerIds.length; i += 30) {
        const batchIds = followerIds.slice(i, i + 30);
        if (batchIds.length > 0) {
          const usersQuery = query(collection(db, 'users'), where('uid', 'in', batchIds));
          const usersSnap = await getDocs(usersQuery);
          usersSnap.forEach(doc => {
            usersData.push({ uid: doc.id, ...doc.data() });
          });
        }
      }

      // For each follower, fetch their most recent review
      const followersWithReviews = await Promise.all(usersData.map(async user => {
          const reviewQuery = query(
              collection(db, 'reviews'),
              where('userId', '==', user.uid),
              orderBy('createdAt', 'desc'),
              limit(1)
          );
          const reviewSnap = await getDocs(reviewQuery);
          let lastReview = null;
          if (!reviewSnap.empty) {
              const reviewData = reviewSnap.docs[0].data();
              lastReview = { ...reviewData, restaurantName: restaurantsMap.get(reviewData.restaurantId) || 'Unknown Restaurant' };
          }
          return { ...user, lastReview };
      }));

      followers.value = followersWithReviews;
      loading.value = false;
    };

    onMounted(fetchFollowers);

    const openReviewsModal = (user) => {
      userForReviews.value = user;
      isReviewsModalVisible.value = true;
    };

    const closeReviewsModal = () => {
      isReviewsModalVisible.value = false;
      userForReviews.value = null;
    };

    return { followers, loading, isReviewsModalVisible, userForReviews, openReviewsModal, closeReviewsModal };
  }
};
</script>

<style scoped>
.followers-page {
  max-width: 800px;
  margin: 0 auto;
}

.no-followers-message {
  text-align: center;
  color: #777;
  padding: 2em;
}

.user-card {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: var(--card-background);
  padding: 1.5em;
  border-radius: 12px;
  box-shadow: 0 4px 15px var(--shadow-color);
  margin-bottom: 1.5em;
  gap: 1.5em;
}

.user-card-content {
  flex-grow: 1;
}

.user-info {
  display: flex;
  align-items: center;
  gap: 1em;
}

.user-avatar {
  width: 60px;
  height: 60px;
  border-radius: 50%;
}

.user-name {
  font-weight: 600;
  font-size: 1.1em;
}

.user-actions {
  display: flex;
  gap: 0.75em;
}

.action-btn {
  background: none;
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 8px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
  color: #555;
}

.action-btn:hover {
  background-color: #f0f0f0;
  border-color: #bbb;
}

.last-review {
  margin-top: 1em;
  padding-top: 1em;
  border-top: 1px solid #eee;
  font-size: 0.9em;
}

.review-header {
  margin-bottom: 0.5em;
  color: #555;
}

.review-body .star-rating {
  margin-bottom: 0.5em;
}

.review-text {
  margin: 0.5em 0 0;
  font-style: italic;
  color: #333;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 400px; /* Adjust as needed */
}

.no-review {
  color: #888;
  font-style: italic;
}
</style>