/**
 * Calculates the distance between two geographical points using the Haversine formula.
 * @param {number | null} lat1 Latitude of the first point.
 * @param {number | null} lon1 Longitude of the first point.
 * @param {number | null} lat2 Latitude of the second point.
 * @param {number | null} lon2 Longitude of the second point.
 * @returns {number|null} The distance in miles, or null if any coordinate is missing.
 */
export function getDistance(lat1, lon1, lat2, lon2) {
  if (lat1 === null || lon1 === null || lat2 === null || lon2 === null) {
    return null;
  }
  if (lat1 === lat2 && lon1 === lon2) {
    return 0;
  }
  const R = 3959; // Radius of the earth in miles
  const dLat = (lat2 - lat1) * Math.PI / 180;
  const dLon = (lon2 - lon1) * Math.PI / 180;
  const a =
    Math.sin(dLat / 2) * Math.sin(dLat / 2) +
    Math.cos(lat1 * Math.PI / 180) * Math.cos(lat2 * Math.PI / 180) *
    Math.sin(dLon / 2) * Math.sin(dLon / 2);
  const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
  return R * c;
}