.review-header { margin-bottom: 0.5em; color: #555; }
.review-body .star-rating { margin-bottom: 0.5em; }
.review-text { margin: 0.5em 0 0; font-style: italic; color: #333; white-space: nowrap; overflow: hidden; text-overflow: ellipsis; max-width: 400px; /* Adjust as needed */ }
